<template>
  <div id="container" class="container">
    <div class="input-area">
      <input id="regex-input" class="regex-input" type="text" v-model="input" @keypress="keyPress($event)" @focus="focus($event)" @blur="blur($event)">
      <button class="submit" @click="fetchWords()" @focus="focus($event)" @blur="blur($event)">Search</button>
    </div>
    <div class="result">
      <table>
        <tr v-for="row in words.length / 4" :key="row">

        </tr>
      </table>
    </div>
  </div>
</template>

<script>
const API = "http://api.words-back.cfapps.io";

export default {
  data() {
    return {
      input: "word.*",
      words: []
    };
  },
  mounted() {
    document.getElementById("regex-input").focus();
  },
  methods: {
    focus(event) {
      event.target.style.zIndex = "2";
    },
    blur(event) {
      event.target.style.zIndex = "1";
    },
    keyPress(event) {
      if (event.key === "Enter") this.fetchWords();
    },
    fetchWords() {
      fetch(API + "/words?regex=" + new RegExp(this.input)).then(resp => {
        resp.json().then(resp => {
          this.words = resp;
        });
      });
    }
  }
};
</script>

<style>
.container {
  max-width: 400px;
  margin: auto;
}
.regex-input {
  font-family: "Courier New", Courier, monospace;
  width: 100%;
  border-radius: 4px 0 0 4px;
}
.submit {
  margin-left: 1px;
  border-radius: 0 4px 4px 0;
}
.input-area {
  display: flex;
}
</style>

